import React from 'react';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import TextField from '@material-ui/core/TextField';
import { Paper, Container } from '@material-ui/core';
import fire from '../firebase'

class Login extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      username : '',
      password : ''
    }  
    this.onChangeusername = this.onChangeusername.bind(this)
    this.onChangepassword = this.onChangepassword.bind(this)
    this.onSubmit = this.onSubmit.bind(this)
  }
  onChangepassword(e){
    this.setState({password:e.target.value})
  }
  onChangeusername(e){
    this.setState({username:e.target.value})
  }
  onSubmit(){
    console.log(this.state)
    if(this.state.username=='user@gmail.com'&&this.state.password=='all'){
    alert('success')
      // window.location.replace("http://localhost:3000/crud")  
    }
    else{
      // window.location.replace("http://localhost:3000/crud")
    }
  }
  render() {
    return (
      <Router>
            <form>
            <Container>
            <Paper style={{width:500,marginLeft:300,marginTop:120}}>  
                <h3 style={{padding:10}}>Sign In</h3>

                <div className="form-group" style={{padding:10}}>
                    <TextField invalid onChange={this.onChangeusername} value={this.state.username} type="email" className="form-control" label="Email" variant="outlined"  />
                </div>

                <div className="form-group" style={{padding:10}}>
                    <TextField onChange={this.onChangepassword} value={this.state.password} type="password" className="form-control" label="Password" variant="outlined" />
                </div>

                <div className="form-group" style={{padding:10}}>
                    <div className="custom-control custom-checkbox" >
                        <input type="checkbox" className="custom-control-input" id="customCheck1" />
                        <label className="custom-control-label" htmlFor="customCheck1">Remember me</label>
                    </div>
                </div>

                <div style={{padding:10}}><button onClick={this.onSubmit} type="submit" className="btn btn-primary btn-block" >Submit</button></div>
                <p style={{padding:10}} className="forgot-password text-right">
                    <a href="#">Forgot password</a>
                </p>
            </Paper>
            </Container>      
            </form>
      </Router>
    );
  }
}


export default Login;
