import React from 'react';
import Times from "../components/Times"
import '../components/Settings.css'
import "bootstrap/dist/css/bootstrap.min.css";
import Buttons from '../components/Buttons'
import { Nav,NavDropdown,Navbar,Button,Form,FormControl } from 'react-bootstrap';
import { Paper } from '@material-ui/core';


class CRUD extends React.Component {
  constructor(props){
    super(props); 
    this.onAdd = this.onAdd.bind(this)
    this.toggleScreenState = this.toggleScreenState.bind(this)
    this.state={
      screenState:true
    }
  }
  onAdd(){
  this.toggleScreenState() 
  }
  toggleScreenState(){
    this.setState({screenState:!this.state.screenState}) 
  }
  render() {
    return (
      <div>
          <Navbar bg="light" variant="light">
          <Navbar.Brand href="#home">Alpha</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="mr-auto">
          <Nav.Link href="#home">Home</Nav.Link>
          <Nav.Link href="#link">Resources</Nav.Link>
          <NavDropdown title="Other Products" id="basic-nav-dropdown">
            <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
            <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
            <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
            <NavDropdown.Divider />
            <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
          </NavDropdown>
          </Nav>
          <Form inline>
            <FormControl type="text" placeholder="Search" className="mr-sm-2" />
            <Button variant="outline-success">Search</Button>
          </Form>
        </Navbar.Collapse>
        </Navbar>
        <div>
        {this.state.screenState&&<Buttons onAddFn={this.onAdd} ></Buttons>}
        </div>
        <div>
        {!this.state.screenState&&<div style={{marginTop:8}}><Button onClick={this.props.onAddFn} variant="danger">Cancel</Button></div>}
        </div>        
      </div>
      
    );
  }
}


export default CRUD;
